@font-face {
  font-family: "Alegreya";
  font-display: swap;
  font-style: normal;
  font-weight: 800;
  src: local(""), url("../fonts/alegreya-v19-latin_cyrillic-800.woff2") format("woff2"), url("../fonts/alegreya-v19-latin_cyrillic-800.woff") format("woff"), url("../fonts/alegreya-v19-latin_cyrillic-800.ttf") format("truetype"), url("../fonts/alegreya-v19-latin_cyrillic-800.svg#Alegreya") format("svg");
}
@font-face {
  font-family: "Alegreya";
  font-display: swap;
  font-style: normal;
  font-weight: 900;
  src: local(""), url("../fonts/alegreya-v19-latin_cyrillic-900.woff2") format("woff2"), url("../fonts/alegreya-v19-latin_cyrillic-900.woff") format("woff"), url("../fonts/alegreya-v19-latin_cyrillic-900.ttf") format("truetype"), url("../fonts/alegreya-v19-latin_cyrillic-900.svg#Alegreya") format("svg");
}
@font-face {
  font-family: "Roboto";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/roboto-v27-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/roboto-v27-latin_cyrillic-regular.woff") format("woff"), url("../fonts/roboto-v27-latin_cyrillic-regular.ttf") format("truetype"), url("../fonts/roboto-v27-latin_cyrillic-regular.svg#Roboto") format("svg");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.page {
  height: 100%;
}

.page_body {
  min-width: 288px;
  min-height: 100%;
  margin: 0;
  font-family: "Roboto", Arial, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
}

.container {
  max-width: 1344px;
  margin: 0 auto;
  border-left: 1px solid rgba(0, 128, 128, 0.5);
  border-right: 1px solid rgba(0, 128, 128, 0.5);
}
@media screen and (max-width: 1344px) {
  .container {
    max-width: 772px;
  }
}
@media screen and (max-width: 804px) {
  .container {
    max-width: 288px;
  }
}

.site-list {
  display: inline-block;
  margin: 0;
  padding: 0;
  font-family: "Alegreya", "Roboto", Arial, sans-serif;
  font-weight: 900;
  font-size: 18px;
  line-height: 1.5;
  list-style-type: none;
}
.site-list__item {
  display: inline-block;
}
.site-list__link {
  display: block;
  padding: 0 30px;
  color: #000;
  text-transform: uppercase;
}
.site-list__link:hover {
  color: #ff3c3c;
}
.site-list__link:active {
  color: #bf221e;
}
.site-list__link--active {
  color: #e52d2d;
}

.phone {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #000;
}
.phone:hover {
  color: #ff3c3c;
}
.phone:active {
  color: #bf221e;
}
.phone__icon {
  width: 40px;
  height: 40px;
}
.phone__info {
  width: 185px;
}
@media screen and (max-width: 1344px) {
  .phone__info {
    display: none;
  }
}
.phone__title {
  font-family: "Alegreya", Arial, sans-serif;
  font-weight: 900;
  font-size: 24px;
  letter-spacing: 1.33px;
}
.phone__description {
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.5;
  color: #848a9a;
}

.cart {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #000;
}
.cart:hover {
  color: #ff3c3c;
}
.cart:active {
  color: #bf221e;
}
.cart__icon {
  width: 40px;
  height: 40px;
}
.cart__info {
  width: 160px;
}
@media screen and (max-width: 1344px) {
  .cart__info {
    display: none;
  }
}
.cart__title {
  font-family: "Alegreya", Arial, sans-serif;
  font-weight: 900;
  font-size: 24px;
  letter-spacing: 1.33px;
}
.cart__description {
  margin: 0;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.5;
  color: #848a9a;
}
.cart__count {
  position: absolute;
  top: -5px;
  left: 30px;
  margin: 0;
  width: 20px;
  height: 20px;
  font-family: "Roboto", Arial, sans-serif;
  font-weight: 500;
  font-size: 11px;
  line-height: 20px;
  text-align: center;
  border-radius: 50%;
  background-color: #e52d2d;
  color: #fff;
}

.lang {
  font-family: "Alegreya", Arial, sans-serif;
  font-weight: 900;
  font-size: 18px;
  letter-spacing: 1px;
  color: #000;
  background-color: #eff0f1;
  border: none;
  border-radius: 50%;
}
.lang:hover {
  color: #ff3c3c;
  cursor: pointer;
}
.lang:active {
  color: #bf221e;
  cursor: pointer;
}

.toggle {
  position: relative;
  width: 24px;
  height: 3px;
  background-color: #000;
  border: none;
  border-radius: 1.5px;
  cursor: pointer;
}
.toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 3px;
  bottom: 8px;
  left: 0;
  background-color: #000;
  border-radius: 1.5px;
}
.toggle::after {
  content: "";
  position: absolute;
  width: 19px;
  height: 3px;
  top: 8px;
  left: 0;
  background-color: #000;
  border-radius: 1.5px;
}

.main-nav {
  display: flex;
  align-items: center;
  padding-top: 46px;
}
.main-nav__logo svg {
  width: 182px;
  height: 84px;
}
@media screen and (max-width: 1344px) {
  .main-nav__logo svg {
    width: 130px;
    height: 60px;
  }
}
@media screen and (max-width: 804px) {
  .main-nav__logo svg {
    width: 85px;
    height: 40px;
  }
}
.main-nav__list {
  width: 400px;
  margin-left: 82px;
  margin-right: 82px;
}
@media screen and (max-width: 1344px) {
  .main-nav__list {
    display: none;
  }
}
.main-nav__phone {
  width: 245px;
  margin-right: 62px;
}
@media screen and (max-width: 1344px) {
  .main-nav__phone {
    width: 40px;
    margin-left: auto;
    margin-right: 32px;
  }
}
@media screen and (max-width: 804px) {
  .main-nav__phone {
    width: 40px;
    margin-left: auto;
    margin-right: 20px;
  }
}
.main-nav__cart {
  width: 215px;
  margin-right: 44px;
}
@media screen and (max-width: 1344px) {
  .main-nav__cart {
    width: 40px;
    margin-right: 70px;
  }
}
@media screen and (max-width: 804px) {
  .main-nav__cart {
    width: 40px;
    margin-right: 25px;
  }
}
.main-nav__lang {
  width: 48px;
  height: 48px;
}
@media screen and (max-width: 804px) {
  .main-nav__lang {
    display: none;
  }
}
.main-nav__toggle {
  width: 24px;
  height: 3px;
  display: none;
}
@media screen and (max-width: 1344px) {
  .main-nav__toggle {
    display: block;
    margin-left: 40px;
  }
}
@media screen and (max-width: 804px) {
  .main-nav__toggle {
    display: block;
    margin-left: 0;
  }
}